<template>
  <div id="app">
    <main>
      <div class="container block">
        <h4>Выберите файл который хотите загрузить</h4>
        <Form :load="load" :setLoad="setLoad" :setFileInfo="setFileInfo" :setPathToFile="setPathToFile" />
        <div class="loader" v-if="load">Loading...</div>
        <Files :pathToFile="pathToFile" :fileInfo="fileInfo"/>
      </div>
    </main>
  </div>
</template>

<script>
import './assets/sass/main.sass';
import Form from "@/components/Form";
import Files from "@/components/Files";
export default {
  name: 'App',
  components: {Files, Form},
  data(){
    return {
      pathToFile: '',
      fileInfo: null,
      load: false,
    }
  },
  methods: {
    setPathToFile(val){
      this.pathToFile = val;
    },
    setLoad(status){
      this.load = status;
    },
    setFileInfo(info){
      this.fileInfo = {
        name: info.name,
        size: info.size,
        edited: info.edited,
        created: info.created,
        id: info.id,
        extname: info.extname
      }
    }
  }
}
</script>

<style>
  .loader{
    text-align: center;
    margin-top: 10px;
  }
</style>
