<template>
  <form @submit="submit" class="load-file-form">
    <label for="file">
      <div class="load-file">{{ this.nameFile || 'Нажмите чтобы загрузить файл' }}</div>
      <input @change="setFile" name="file" type="file" id="file">
    </label>
    <button class="btn btn-danger" :disabled="load">Загрузить</button>
  </form>
</template>

<script>
export default {
  name: "Form",
  props: ['setPathToFile', 'setFileInfo', 'setLoad', 'load'],
  data(){
    return {
      nameFile: '',
      file: null,
    }
  },
  methods: {
    setNameFile(name){
      this.nameFile = name;
    },
    async submit(e){
      e.preventDefault();
      if(!this.file){
        return alert('Вы не выбрали файл');
      }
      this.setLoad(true);
      let res = await this.fetchFile();
      let json = await res.json();
      this.setLoad(false);
      this.setFileInfo(json.data);

    },
    async fetchFile(){
      let formData = new FormData();
      formData.append('file', this.file);
      return await fetch('https://adisk-backend.herokuapp.com/api/loadFile', {
        method: 'POST',
        body: formData
      });
    },
    setFile(e){
      let file = e.target.files[0];
      this.file = file;
      this.setPathToFile(e.target.value);
      this.setNameFile(file.name);
    }
  }
}
</script>

